"use client"
import { useEffect, useState } from 'react'
import CategoryTabs from './components/CategoryTabs'
import SubCategoryTabs from './components/SubCategoryTabs'


export default function HomePage() {
  const [category, setCategory] = useState('')
  const [subCategory, setSubCategory] = useState('')
  const [items, setItems] = useState([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  useEffect(() => {
    if (!category) return // don’t fetch until category is selected

    const run = async () => {
      setLoading(true)
      setError('')
      try {
        // ✅ If subCategory is empty, fetch all professionals under that category
        const data = await getProfessionals({
          category,
          subCategory: subCategory || 'All', // fallback value
        })

        console.log('Fetched data:', data) // helpful for debugging

        setItems(data?.items || [])
      } catch (e) {
        setError(e.message || 'Failed to load data')
        setItems([])
      } finally {
        setLoading(false)
      }
    }

    run()
  }, [category, subCategory])

  return (
    <main className="min-h-screen bg-white pb-16">
      {/* Category Tabs */}
      <CategoryTabs
        value={category}
        onChange={(val) => {
          setCategory(val)
          setSubCategory('') // reset subcategory when category changes
        }}
      />

      {/* SubCategory Tabs */}
     

      
    </main>
  )
}
